import { Component, ViewChild } from '@angular/core'
import { Router, ActivatedRoute } from '@angular/router'
import {ClientService, UtilisService} from '../../../services'
import {Cliente, ClienteEntrega} from '../../../models'
import {Observable} from 'rxjs/Observable'

@Component({
  moduleId: module.id,
  templateUrl: 'registrar.component.html',
  styleUrls: ['registrar.component.scss']
})
export class RegistrarClienteComponent {
  cliente: Cliente
  entrega: ClienteEntrega
  estados$: Observable<any>
  ciudades$: Observable<any>
  estados2$: Observable<any>
  ciudades2$: Observable<any>
  editarSucursal: boolean = false
  editar
  pos
  sucursales = []
  titulo = 'Registrar'

  constructor(
    private clientService: ClientService,
    private router: Router,
    private route: ActivatedRoute,
    private utilsService: UtilisService
  ) {
    const {url} = router
    this.checkIfUpdate(url)
  }

  ngOnInit() {
    this.cleanCliente()
    this.cleanEntrega()
    this.estados$ = this.utilsService.getEstados()
    this.estados2$ = this.utilsService.getEstados()
  }

  getCiudades (estado) {
    this.ciudades$ = this.utilsService.getCiudades(estado)
  }

  getCiudades2 (estado) {
    this.ciudades2$ = this.utilsService.getCiudades(estado)
  }

  checkIfUpdate (url) {
    if (/editar/.test(url)) {
      this.editar = true
      this.clientService
        .getClient(this.route.snapshot.params['id'])
        .subscribe(data => {
          this.titulo = 'Editar'
          this.sucursales = data.entrega.slice()
          this.ciudades$ = this.utilsService.getCiudades(data.estado)
          delete data.entrega
          this.cliente = data
          this.cleanEntrega()
        })
    }
  }

  createClient() {
    if (this.entrega.direccion !== '') {
      if (this.editarSucursal) {
        this.sucursales[this.pos] = this.entrega
        this.editarSucursal = false
      } else {
        this.sucursales.push(this.entrega)
      }
    }

    this.cliente.entrega = [...this.sucursales]

    if (this.editar) {
      console.log(this.cliente)
      this.clientService
        .updateClient(this.cliente)
        .subscribe(() => this.redirect())
    } else {
      this.clientService
        .createClient(this.cliente)
        .subscribe(() => this.redirect())
    }
  }

  addSucursal() {
    if (this.entrega.direccion !== '') {
      if (this.editSucursal) {
        this.sucursales[this.pos] = this.entrega
        this.editarSucursal = false
        this.cleanEntrega()
      } else {
        this.sucursales.push(this.entrega)
        this.cleanEntrega()
      }
    }
  }

  editSucursal (index) {
    this.entrega = Object.assign({}, this.sucursales[index])
    this.editarSucursal = true
    this.pos = index
    this.getCiudades2(this.entrega.estado)
  }

  deleteSucursal (index) {
    this.sucursales.splice(index, 1)
  }

  redirect () {
    this.cleanCliente()
    this.cleanEntrega()
    this.router.navigate(['user', 'clientes', 'lista'])
  }

  cleanCliente () {
    this.cliente = {
      nombre: "",
      estado: "",
      rfc: "",
      direccion: "",
      cp: "",
      ciudad: "",
      colonia: "",
      email: "",
      telefono: "",
      entrega: []
    }
  }

  cleanEntrega () {
    this.entrega = {
      responsable: "",
      estado: "",
      direccion: "",
      cp: "",
      ciudad: "",
      colonia: "",
      telefono: "",
      sucursal: ""
    }
  }
}



