import { Component } from '@angular/core'
import {Router} from '@angular/router'
import {Observable} from 'rxjs/Observable'
import {Subject} from 'rxjs/Subject'
import {AuthService, ClientService, UtilisService} from '../../../services'
import {Cliente, ClienteFiltros, ListaClientes} from '../../../models'
import 'rxjs/add/operator/share'
import 'rxjs/add/operator/debounceTime'
import 'rxjs/add/operator/distinctUntilChanged'
import 'rxjs/add/operator/startWith'

declare const $: any

@Component({
  moduleId: module.id,
  templateUrl: 'lista.component.html',
  styleUrls: ['lista.component.scss']
})
export class ListaClienteComponent {
  user$: Observable<any>
  clients$: Observable<ListaClientes>
  client$: Observable<Cliente>
  estados$: Observable<any>
  ciudades$: Observable<any> | undefined
  filtro$ = new Subject<any>()
  filter: ClienteFiltros

  constructor(
    private authService: AuthService,
    private clientService: ClientService,
    private router: Router,
    private utilsService: UtilisService
  ) {
      this.user$ = authService.USER
    }

  ngOnInit () {
    this.filter = {
      limit: 25,
      estado: 1,
      ciudad: 0,
      cliente: '',
      total: 0,
      skip: 0
    }

    this.getClients()

    this.estados$ = this.utilsService
      .getEstados()

    this.filtro$
        .debounceTime(400)
        .distinctUntilChanged()
        .subscribe(f => this.search(f))
  }

  search ({key, value}) {
    if (key === 'estado') {
      if (value !== 1 && value !== '1') {
        this.ciudades$ = this.utilsService
          .getCiudades(value)
      } else {
        this.ciudades$ = undefined
        Object.assign(this.filter, {ciudad: 0})
      }
    }
    if (key === 'limit') {
      Object.assign(this.filter, {[key]: parseInt(value, 10)})
    }
    console.log(this.filter)
    this.getClients()
  }

  viewDetails (id) {
    this.client$ = this.clientService
      .getClient(id)
    $('#clientModal').modal('show')
  }

  getCiudades (estado) {
    this.ciudades$ = this.utilsService
      .getCiudades(estado)
  }

  editClient (id) {
    this.router.navigate(['admin', 'editar', 'cliente', id])
  }

  getClients () {
    this.clients$ = this.clientService
      .getFilterClients(this.filter)
      .do((resp: ListaClientes) => this.setFilters(resp))
      .map((resp: ListaClientes) => resp)
      .startWith()
      .share()
  }

  setFilters (lc: ListaClientes) {
    this.filter = lc.filters
    return lc
  }

  deleteClient (id) {
    this.clientService
      .deleteClient(id)
      .subscribe(data => this.getClients())
  }
}



